/*
[12]   //  Footer  & All Pages         //  2023-1-13


*/
footer {
  color: #fff;
  height: 200px;
  font-family: "Roboto", sans-serif;
  width: 100%;
  background-image: linear-gradient(to top, #050505 50%, transparent 50%);
  background-image: -webkit-linear-gradient(to top, #050505 50%, transparent 50%);
}
footer .alexeg {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
footer .alexeg span {
  font-size: 1rem;
  font-weight: 500;
}
@media (max-width: 400px) {
  footer .alexeg span {
    font-size: 0.7rem;
  }
}
footer .alexeg img {
  width: 25px;
  margin-top: 3px;
  margin-inline: 3px;
}
@media (max-width: 400px) {
  footer .alexeg img {
    width: 15px;
    margin-top: 1px;
    margin-inline: 1px;
  }
}
footer .accounts {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
footer .accounts a {
  color: #fff;
}
footer .accounts i {
  transition: color 400ms;
  cursor: pointer;
  font-size: 2.5rem;
  margin-inline: 3px;
}
@media (max-width: 400px) {
  footer .accounts i {
    font-size: 1.5rem;
    margin-inline: 3px;
  }
}
footer .accounts i:hover {
  color: #00a2e3;
}
footer .footer__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  height: 50%;
}
footer .line {
  background-color: inherit;
  position: relative;
  width: 100%;
  height: 100px;
  overflow: hidden;
}
footer .line::before {
  content: "";
  width: 100%;
  height: 1px;
  position: absolute;
  bottom: -2px;
  left: 0;
  box-shadow: 0 0 40px 15px #2e7bff;
}

.html__page__footer {
  width: calc(100% - 250px);
  margin-left: 250px;
}

::-webkit-scrollbar {
  width: 0.9em;
  height: 0.9em;
  background-color: #0a0a0a;
}

::-webkit-scrollbar-track {
  background-image: linear-gradient(to top, rgba(102, 0, 51, 0.255), rgba(90, 0, 102, 0));
  border-radius: 100vw 0 0 100vw;
  margin-top: 2.5em;
}

::-webkit-scrollbar-thumb {
  background: rgba(169, 4, 4, 0.459);
  border-radius: 100vw;
}

::-webkit-scrollbar-thumb:hover {
  background: rgba(169, 4, 4, 0.659);
}

/* ***firefox*** */
@supports (scrollbar-color: #ff0000 #490025) {
  * {
    scrollbar-color: rgba(169, 4, 4, 0.459) #470024;
    scrollbar-width: thin;
  }
}
.manga__body {
  --bg-body: hsl(0, 0%, 2%);
  --bg-body-light: hsl(0, 0%, 90%);
  --boxActive: hsla(283, 82%, 20%, 0.545);
  --boxActive-light: hsl(268, 100%, 32%);
  --boxBorder: hsla(0, 0%, 100%, 0.425);
  --boxHover: hsla(0, 0%, 100%, 0.06);
  --box-bg: hsl(0, 0%, 8%);
  --box-bg-light: hsl(0, 0%, 3%);
  --box-t: hsl(0, 0%, 60%);
  --card-title: hsl(0, 0%, 100%);
  --card-title-bg-lg: hsl(0, 0%, 0%);
  --card-bg-lg: hsla(0, 0%, 0%, 0.35);
  --dark-theme-btn: hsl(0, 0%, 100%);
  --dark-theme-btn-circle: hsl(0, 0%, 0%);
  --light-theme-btn: hsl(0, 0%, 0%);
  --light-theme-btn-circle: hsl(0, 0%, 100%);
}

.manga__body {
  background-color: var(--bg-body);
  min-height: 100vh;
  transition: 900ms background-color ease-in-out;
}
.manga__body main {
  position: relative;
  padding-top: 35px;
}
.manga__body main .manga__contaner {
  min-width: 370px;
  max-width: 1000px;
  min-height: 500px;
  margin-inline: auto;
  padding: 50px 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  row-gap: 30px;
}

.manga__body header {
  --banner-bg-blur: -1;
  height: 400px;
  position: relative;
  font-family: "Public Sans", sans-serif;
}
.manga__body header .banner-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  filter: blur(5px);
  z-index: var(--banner-bg-blur);
}
.manga__body header .info {
  background-color: rgba(0, 0, 0, 0.712);
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 3fr;
}
@media (max-width: 1035px) {
  .manga__body header .info {
    grid-template-columns: 1fr 2fr;
  }
}
.manga__body header .info__manga-img {
  display: flex;
  justify-content: center;
  align-items: center;
}
.manga__body header .info__manga-img__manga-cover {
  width: 246px;
  height: 360px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 1035px) {
  .manga__body header .info__manga-img__manga-cover {
    width: 185px;
    height: 265px;
  }
}
.manga__body header .info__manga-info {
  padding: 50px 0 0 0;
}
.manga__body header .info__manga-info h1 {
  color: white;
  text-shadow: 0 0 1px;
  margin-bottom: 20px;
}
.manga__body header .info__manga-info h2 {
  color: rgba(255, 255, 255, 0.812);
  text-shadow: 0 0 1px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.manga__body header .info__manga-info p {
  color: rgba(255, 255, 255, 0.522);
  margin-bottom: 5px;
  line-height: 1.3;
}

