.manga__body .controller__container {
  background-color: inherit;
  font-family: "Roboto", sans-serif;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}
.manga__body .controller__container .cards {
  margin: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 2px;
  width: auto;
  height: 50px;
  border-radius: 0;
}
.manga__body .controller__container .cards:hover > .card::after {
  opacity: 1;
}
.manga__body .controller__container .card {
  cursor: pointer;
  height: 50px;
  width: 60px;
  display: flex;
  flex-direction: column;
  position: relative;
}
.manga__body .controller__container .card:hover::before {
  opacity: 1;
}
.manga__body .controller__container .card::before,
.manga__body .controller__container .card::after {
  content: "";
  height: 100%;
  width: 100%;
  opacity: 0;
  position: absolute;
  left: 0;
  top: 0;
  transition: opacity 500ms;
}
.manga__body .controller__container .card::before {
  background: radial-gradient(400px circle at var(--mouse-x) var(--mouse-y), var(--boxHover), transparent 40%);
  z-index: 3;
}
.manga__body .controller__container .card::after {
  background: radial-gradient(400px circle at var(--mouse-x) var(--mouse-y), var(--boxBorder), transparent 30%);
  z-index: 1;
}
.manga__body .controller__container .card > .card-content {
  background-color: var(--box-bg);
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  inset: 1px;
  padding: 10px;
  position: absolute;
  z-index: 2;
}
.manga__body .controller__container .card > .card-content span {
  color: var(--box-t);
  font-weight: 600;
  font-size: 1rem;
  margin: auto;
}
.manga__body .controller__container .card > .active {
  background-color: var(--boxActive);
}

.manga__card {
  width: 300px;
  height: 430px;
  text-align: center;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}
.manga__card img {
  max-width: 100%;
  max-height: 100%;
}
.manga__card::after {
  position: absolute;
  font-family: "Roboto", sans-serif;
  color: var(--card-title);
  box-sizing: border-box;
  text-align: center;
  padding-top: 40px;
  width: 100%;
  height: 100px;
  bottom: 0;
  left: 0;
  background-image: -moz-linear-gradient(to top, var(--card-title-bg-lg), transparent);
  background-image: -webkit-linear-gradient(to top, var(--card-title-bg-lg), transparent);
  background-image: linear-gradient(to top, var(--card-title-bg-lg), transparent);
  z-index: 10;
  opacity: 0;
  transition: opacity 300ms;
}
.manga__card:hover::after {
  opacity: 1;
}
.manga__card::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  transition: background-color 350ms;
}
.manga__card:hover:before {
  background-color: var(--card-bg-lg);
}

.manga__body .controller__container .light-dark-btn {
  width: 60px;
  height: 27px;
  background-color: var(--dark-theme-btn);
  border-radius: 50px;
  position: relative;
  transition: 3150ms background-color ease;
}
.manga__body .controller__container .light-dark-btn .circle {
  width: 20px;
  height: 20px;
  background-color: var(--dark-theme-btn-circle);
  border-radius: 100%;
  position: absolute;
  top: 3.5px;
  left: 3px;
  transition: 350ms transform ease;
}

.light-theme {
  background-color: var(--bg-body-light);
}
.light-theme .controller__container .cards .card > .active {
  background-color: var(--boxActive-light);
}
.light-theme .controller__container .cards .card-content {
  background-color: var(--box-bg-light);
}
.light-theme .controller__container .light-dark-btn {
  background-color: var(--light-theme-btn);
}
.light-theme .controller__container .light-dark-btn .circle {
  background-color: var(--light-theme-btn-circle);
  transform: translateX(32.5px);
}

.manga__body .reader {
  --reader: 100;
  --bg-hide: -101;
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  z-index: var(--reader);
}
.manga__body .reader .bg-hide {
  position: absolute;
  height: 100%;
  width: 100%;
  background: rgba(0, 0, 0, 0.849);
  z-index: var(--bg-hide);
}
.manga__body .reader .close-reader {
  cursor: pointer;
  z-index: 99;
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
  border: none;
  padding: 5px 25px;
  color: #ffafcc;
  font-family: roboto;
  font-weight: 600;
  font-size: 1.1rem;
  background-color: rgba(0, 174, 255, 0.753);
}
.manga__body .reader .close-reader:hover {
  text-shadow: 0 0 2px;
  background-color: rgba(0, 174, 255, 0.425);
  cursor: pointer;
}
.manga__body .btn {
  cursor: pointer;
  z-index: 99;
  position: absolute;
  border: none;
  padding: 5px 15px;
  color: white;
  background-color: rgba(0, 174, 255, 0.74);
}
.manga__body .btn:hover {
  text-shadow: 0 0 2px;
  background-color: rgba(0, 174, 255, 0.425);
  cursor: pointer;
}

.noscroll {
  overflow: hidden;
}

.manga-page {
  width: 450px;
  height: 100%;
  margin-inline: auto;
  position: relative;
  overflow: auto;
}
.manga-page img {
  margin-bottom: 5px;
}

.hide {
  display: none;
}

.manga__body header .info .next-back {
  width: 80px;
  height: 35px;
  padding: 5px 5px 0;
  margin-inline: auto;
  display: flex;
  position: absolute;
  bottom: 10px;
  right: 25px;
}
.manga__body header .info .next-back div:first-child,
.manga__body header .info .next-back div:last-child {
  height: 25px;
  width: 25px;
  border: solid 2px;
  margin-inline: 5px;
  border-color: rgba(255, 255, 255, 0.547) rgba(255, 255, 255, 0.547) transparent transparent;
  transition: border-color 300ms;
  cursor: pointer;
  transform: rotate(45deg);
}
.manga__body header .info .next-back div:first-child {
  border-color: transparent transparent rgba(255, 255, 255, 0.547) rgba(255, 255, 255, 0.547);
}
.manga__body header .info .next-back div:first-child:hover {
  border-color: transparent transparent rgba(255, 255, 255, 0.89) rgba(255, 255, 255, 0.89);
}
.manga__body header .info .next-back div:first-child:active {
  border-color: transparent transparent rgba(255, 255, 255, 0.288) rgba(255, 255, 255, 0.288);
}
.manga__body header .info .next-back div:last-child:hover {
  border-color: rgba(255, 255, 255, 0.89) rgba(255, 255, 255, 0.89) transparent transparent;
}
.manga__body header .info .next-back div:last-child:active {
  border-color: rgba(255, 255, 255, 0.288) rgba(255, 255, 255, 0.288) transparent transparent;
}

